<template>
    <div class="cart-list">
        <header>
            <h4></h4>
            <a 
                href="javascript:;" 
                class="clear"
                @click="clearCartList">
                <i class="iconfont icon-clear"></i>
                
            </a>
        </header>
        <section>
            <swiper class="cart-list__swiper" :options="swiperOption">
                <swiper-slide v-for="(item, index) in goodsList" :key="index">
                    <h5>{{ item.goods_name }}<span>{{ item.goods_attr }}</span></h5>
                    <em class="price">{{ item.subtotal }}</em> 
                    <computed-num 
                        @computed-num="computedNum"
                        :options="{
                            index,
                            id:item.rec_id,
                            number:item.goods_number,
                            goodsId:item.goods_id
                        }">
                    </computed-num>
                </swiper-slide> 
            </swiper>
        </section>
    </div>
</template>

<script>
// node library
import qs from 'qs'

// custom components
import ComputedNum from 'components/ComputedNum'

// third party components
import {
    swiper,
    swiperSlide
} from 'vue-awesome-swiper'

export default {
    name: 'cart-list',
    components: {
        swiper,
        swiperSlide,
        ComputedNum
    },
    data() {
        return {
            swiperOption: {
                notNextTick: true,
                direction: 'vertical',
                slidesPerView: 'auto',
                freeMode: true
            },
            goodsList:[],
            resArr:[]
        }
    },
    activated () {
        this.getCartList()
    },
    methods: {
        /**
         * 
         */
        clearCartList(){
            this.$http.post(this.url('cart&a=drop_goods&ajax=1'),qs.stringify({
                id:`${this.resArr.join(',')},`
            })).then(() => {
                this.goodsList = []
                this.$emit('close-cart-list',false)
                this.$emit('clear-goods-number')
            })
        },
        /**
         * 
         * 
         */
        getCartList() {
            this.$http.post(this.url('cart')).then(({ data }) => {
                let {
                cart_show: cartShow,
                    goods_list,
                    error,
                    total
            } = data
                if (error == 0) {
                    let arr = []
                    let resArr = []
                    goods_list.forEach(item => {
                        item.goods_list.forEach(item => {
                            arr.push(item)
                            resArr.push(item.rec_id)
                        })
                    })
                    this.goodsList = arr.filter(item => {
                        return item.goods_number != 0
                    })
                    this.resArr = resArr
                }
            })
        },
        /**
         * 
         * 
         * @param {*String} type -  plus  less
         * @param {*Object} o -   id(index)(number)
         */
        computedNum(type, o) {
            let number = this.goodsList[o.index].goods_number
            if (type == "plus") {
                this.goodsList[o.index].goods_number = parseInt(number) + 1
                this.cartGoodsNumber({
                    id: o.id,
                    number: this.goodsList[o.index].goods_number,
                    index: o.index,
                    goodsId:o.goodsId,
                    type
                })
            } else {
                if (number <= 1) {
                    this.$http.post(this.url('cart&a=delete_cart&ajax=1'), qs.stringify({
                        id: o.id
                    })).then(({ data: { error } }) => {
                        if (error == 0) {
                            this.goodsList.splice(o.index, 1)
                            this.resArr.splice(o.index,1)
                        }
                       this.$emit('update-goods-number',o)
                    })
                } else {
                    this.goodsList[o.index].goods_number = parseInt(number) - 1
                    this.cartGoodsNumber({
                        id: o.id,
                        number: this.goodsList[o.index].goods_number,
                        index: o.index,
                        goodsId:o.goodsId,
                        type
                    })
                }
            }
        },
        /**
         * 
         * 
         * @param {*Object} o -   id(index)(number)
         * */
        cartGoodsNumber(o){
            let shopPrice = this.goodsList[o.index].goods_price.slice(1)
            let market = this.goodsList[o.index].goods_price.slice(0,1)
            this.goodsList[o.index].subtotal = market + (parseFloat(shopPrice) * o.number).toFixed(2)
            this.$http.post(this.url('cart&a=cart_goods_number&ajax=1'),qs.stringify({
                id:o.id,
                number:o.number,
                arr:this.resArr.join(','),
                none:0
            })).then(({ data:{ error } }) => {
                if(error == 0){
                    this.$emit('update-goods-number',o)
                }
            })
        }
    },
    watch: {
        /**
         *   computed-cart 
         * 
         * 
         */
        'goodsList':{
            deep:true,
            handler(val,oldVal){
                let price = 0
                let number = 0
                let market = ''
                val.forEach(item => {
                    if (market === '') {
                        market = item.subtotal.slice(0,1)
                    }
                    price += parseFloat(item.subtotal.slice(1))
                    number += parseInt(item.goods_number)
                })
                price = `${market}${price.toFixed(2)}`
                this.$emit('computed-cart', {
                    number,
                    price
                })
            }
        },
        'resArr':{
            deep:true,
            handler(val,oldVal){
                this.$emit('get-cart-list-arr',val)
            }
        }
    }
}
</script>

<style lang="scss" scoped>
@import '../assets/style/config';
@import '../assets/style/mixins/common';
.cart-list {
    width: 100%;
    position: fixed;
    bottom: 4.8rem;
    background: #fff;
    z-index: 123;
    header {
        height: 4rem;
        font-size: 1.4rem;
        padding: 0 1.3rem;
        background: #f3f3f3;
        @include direction(center, space-between);
        a {
            color: #787878
        }
    }
}

.cart-list__swiper {
    max-height: 28rem;
    min-height:6rem;
    padding: 0 1.3rem;
    padding-bottom: .6rem;
    .swiper-slide {
        height:6rem;
        border-bottom: 1px solid #f3f3f3;
        @include direction(center,space-between);
        // @include disBox()
        .to-cart{
            position:relative;
        }
        h5 {
            @include flex1-1();
            font-weight: normal;
            font-size: 1.44rem;
            max-width: 16.66rem;
            margin-right:1rem;
            white-space: nowrap;
            @include ellipses();
            span{
                display:block;
                font-size:1rem;
                color:#999;
                white-space: nowrap;
                @include ellipses();
            }
        }
        .price{
            margin-right:1rem;
            font-size:1.5rem;
            width:8rem;
            text-align: center;
            white-space: nowrap;
            @include ellipses();
            color:$color;
        }
        &:last-of-type {
            border-bottom: 0;
        }
    }
}
</style>




