<template>
<div>
     <footer :class="{
         disable:!bPrice
     }">
        <div class="cart" @click="showCart()">
            <em class="number" v-show="bPrice">{{ options.number }}</em>
            <i class="iconfont icon-cart"></i>
        </div>
        <div class="right">
            <em class="price">
                {{ bPrice ? options.price : '' }}
            </em>
            <a :href="url('flow')"></a>
        </div>
    </footer>
</div>
</template>

<script>
// third party components
import {
    swiper,
    swiperSlide
} from 'vue-awesome-swiper'

import{
    Loading
} from 'element-ui'

export default {
    name:'footer-cart',
    props:['bCartList','options'],
    components: {
      swiper,
      swiperSlide,
      Loading  
    },
    data() {
        return {
        }
    },
    created() {
    },
    methods: {
        showCart(){
            if(this.bPrice){
                this.$emit('update:bCartlist',!this.bCartList)
            }
        }
    },
    computed: {
        bPrice(){
            return parseFloat(this.options.price.slice(1)) <= 0 
                && this.options.number == 0 
                ? false
                : true
        }
    }
}
</script>

<style lang="scss" scoped>
@import '../assets/style/config';
@import '../assets/style/mixins/common';
footer {
    position: fixed;
    z-index: 124;
    width: 100%;
    height: 4.8rem;
    background: #fff;
    bottom: 0;
    border-top: 1px solid #e5e5e5;
    .cart {
        position: absolute;
        top: -1rem;
        left: 1rem;
        color: #fff;
        width: 4.8rem;
        height: 4.8rem;
        text-align: center;
        line-height: 4.8rem;
        border-radius: 2.4rem;
        background: $color;
        i {
            font-size: 2.6rem;
        }
        em {
            position: absolute;
            font-size: 1.2rem;
            right: -.4rem;
            top: -.4rem;
            padding: 0 .4rem;
            height: 1.8rem;
            line-height: 1.8rem;
            min-width: 1.8rem;
            border-radius: .9rem;
            background: red;
            border: 1px solid #fff;
        }
    }
    .right {
        position: absolute;
        height: 4.8rem;
        line-height: 4.8rem;
        left: 7rem;
        right: 0;
        @include direction(center, space-between);
        em {
            color: $color;
            font-size: 1.6rem;
        }
        a {
            background: $color;
            color: #fff;
            font-size: 1.6rem;
            padding: 0 1.6rem;
        }
    }
    &.disable{
        .cart, .right a,{
            background:#bbb
        }
        em{
            color:#999;
            // font-size:1.6rem;
        }
    }
}
</style>





