<template>
    <div 
        class="to-cart" 
        :class="{
        active:options.number > 0}">
        <a  
            href="javascript:;" 
            class="less"
            :class="{
                disable:options.moreAttr
            }"
            @click="computedNum({
                moreAttr:options.moreAttr,
                type:'less'
            })"
            v-if="bLess">
            <i class="iconfont icon-less"></i>
            </a>
        <span v-if="bLess">{{ options.number }}</span>
        <a 
            href="javascript:;" 
            class="plus" 
            @click="computedNum({
                type:'plus'
            })">
            <i class="iconfont icon-plus"></i>
        </a>
    </div>
</template>

<script>
// event bus
import bus from 'src/eventBus'

export default {
    name: 'computendNum',
    props: {
        options:{},
        bLess:{
            type:Boolean,
            default:true
        }
    },
    data() {
        return {

        }
    },
    created() {
    },
    methods: {
        computedNum(o) {
            if(o.moreAttr != 1){
                this.$emit('computed-num',o.type,this.options)
            }
        }
    }
}
</script>

<style lang="scss" scoped>
@import '../assets/style/config';
@import '../assets/style/mixins/common';

.to-cart {
    position: relative;
    width: 6.8rem;
    height: 2rem;
    line-height: 2rem;
    overflow: hidden;
    .plus,
    .less,
    span {
        @include transition(all, .3s);
        text-align: center;
        left: 4.6rem;
        opacity: 0;
        visibility: hidden;
    }
    .plus,
    .less {
        width: 2rem;
        height: 2rem;
        position: absolute;
        border-radius: 1rem;
        border: 1px solid $color;
        i {
            font-size: 1rem;
        }
    }
    .plus {
        opacity: 1;
        visibility: visible;
    }
    span {
        text-align: center;
        z-index: 1;
        position: absolute;
        display: inline-block;
        font-size: 1.38rem;
        color: #666;
        margin: 0 .6rem;
        width: 1.4rem;
    }
    .less {
        z-index: 2;
        color: $color;
        transform: rotate(0deg);
        &.disable{
            border: 1px solid lighten($color,24%);
            color:lighten($color,24%);
        }
    }
    .plus {
        z-index: 3;
        color: #fff;
        background: $color;
    }
    &.active {
        .less,
        span {
            text-align: center;
            opacity: 1;
            visibility: visible;
        }
        .less {
            left: 0;
            transform: rotate(360deg);
        }
        span {
            left: 2rem;
        }
    }
}
</style>





