<template>
    <div class="category-attr">
        <i class="iconfont icon-close" @click="closeAttr()"></i>
        <section class="category-attr__con">
            <h5>{{ data.name }}</h5>
            <swiper :options="attrOption" ref="attrSwiper">
                <template v-if="attrList.length > 0">
                    <swiper-slide 
                        v-for="(item, index) in attrList"
                        :key="index">
                        <div class="category-list">
                            <h5>{{ item.name }}</h5>
                            <section>
                                <label 
                                    v-for="(itemAttr,indexAttr) in item.values" 
                                    :key="indexAttr"
                                    :class="{
                                        active:attrActive[index] == indexAttr
                                    }"
                                    @click="computedAttr(index,indexAttr)">
                                    {{ itemAttr.label }}
                                </label>
                            </section>
                        </div>
                    </swiper-slide>
                </template>
            </swiper>
            <footer>
                <em class="price">{{ shopPrice }}</em>
                <span><em>{{ nAttr }}</em></span>
                <a 
                    href="javascript:;"
                    :class="{
                        disable:!bContrim
                    }"
                    @click="contrim()">
                    
                </a>
            </footer>
        </section>
    </div>
</template>

<script>
// event bus
import bus from 'src/eventBus'

// node library
import qs from 'qs'

// third party components
import {
    swiper,
    swiperSlide
} from 'vue-awesome-swiper'

export default {
    name: 'category-attr',
    props: ['data'],
    components: {
        swiper,
        swiperSlide
    },
    data() {
        return {
            attrOption: {
                notNextTick: true,
                direction: 'vertical',
                slidesPerView: 'auto',
                freeMode: true
            },
            bCartList: true,
            attrActive: [],
            attrList:[],
            resAttr:{
                id:this.data.id,
                attr:'',
                goods_attr:'',
                number:1,
            },
            shopPrice:this.data.price,
            bContrim:true,
            nAttr:0
        }
    },
    created () {
        let attr = []
        let goodsAttr = []
        if(this.data.attr.length != 0 ){
            for(let key in this.data.attr){
                this.attrActive.push(0)
                goodsAttr.push(key)
                this.attrList.push(this.data.attr[key])
            }
            this.resAttr.goods_attr = goodsAttr.join(',')
        }
        this.getAttr()
        this.sendAttr()
    },
    mounted () {
    },
    methods: {
        closeAttr() {
            this.$emit('update:attr', false)
        },
        contrim() {
            if (this.bContrim) {
                this.addToCart().then(({ data }) => {
                    if (data.error == 0) {
                        this.data.number++
                        this.$emit('updateNumber', {
                            index: this.data.index,
                            number: this.data.number,
                            id: this.data.id,
                            addNumber:this.resAttr.number,
                            goodsNumber:data.goods_number,
                            shopPrice:this.shopPrice
                        })
                        this.$emit('update:attr', false)
                    }
                })
            }
        },
        addToCart() {
            return this.$http.post(this.url('cart&a=add_to_cart'), qs.stringify({
                goods: JSON.stringify({
                    spec: this.resAttr.attr.split(','),
                    goods_id: this.data.id,
                    store_id: 0,
                    number: this.resAttr.number,
                    parent: 0
                })
            }))
        },
        getAttr() {
            let attr = []
            this.attrList.forEach((item, index) => {
                attr.push(item.values[this.attrActive[index]].id)
            })
            this.resAttr.attr = attr.join(",")
        },
        computedAttr(index, indexAttr) {
            this.$set(this.attrActive, index, indexAttr)
            this.getAttr(indexAttr)
            this.sendAttr()
        },
        sendAttr() {
            this.$http.post(this.url('goods&a=price'), qs.stringify({
                id: this.resAttr.id,
                attr: this.resAttr.attr,
                goods_attr: this.resAttr.goods_attr,
                number: this.resAttr.number
            })).then(({ data }) => {
                this.shopPrice = data.shop_price
                this.nAttr = data.attr_number
                data.attr_number > 0 
                    ? this.bContrim = true 
                    : this.bContrim = false
            })
        }
    }
}

</script>

<style lang="scss" scoped>
@import '../assets/style/config';
@import '../assets/style/mixins/common';
.category-attr {
    position: fixed;
    width: 86%;
    background: #fff;
    z-index: 125;
    padding: 1.8rem;
    left:50%;
    top:22%;
    margin-left:-43%;
    border-radius: .6rem;
    overflow:hidden;
    padding-bottom:5rem;
    .icon-close{
        position:absolute;
        right:1.6rem;
        top:1.2rem;
        color:#d8d8d8;
        font-size:1.6rem;
    }
}

.category-attr__con {
    border-radius: .4rem;
    overflow:hidden;
    > h5{
        padding-left: 1.3rem;
        padding-right: 2rem;
        color:#3d3d3d;
        font-size:1.5rem;
        text-align: center;
        white-space: nowrap;
        @include ellipses();
    }
    footer{
        position:absolute;
        bottom:0;
        left:0;
        right:0;
        padding:.8rem 1.2rem;
        margin-top:1rem;
        background:#f8f8f8;
        overflow:hidden;
        @include direction(center,space-between);
        .price{
            font-size:1.7rem;
            color: $color;
            float:left
        }
        span{
            color:#888;
            font-size:1.2rem;
            em{
                color:#333;
            }
        }
        a{
            float:right;
            border-radius: 4px;
            padding:0 1.6rem;
            background-color:$color;
            height:2.6rem;
            line-height: 2.6rem;
            color:#fff;
            font-size:1.2rem;
            &.disable{
                background:#aaa;
            }
        }
    }
    .swiper-container {
        overflow: hidden;
        margin-top:.8rem;
        max-height: 26rem;
    }
    .category-list{
        margin:.6rem 0;
        section{
            overflow:hidden;
            label{
                font-size:1.26rem;
                padding: .34rem 1.6rem;
                display: block;
                float:left;
                text-align: center;
                background-color:#f8f8f8;
                border-radius: 4px;
                color: #666;
                margin: .4rem;
                &.active{
                    border-color: $color;
                    color:$color;
                    background-color:#f9f0f0;
                }
            }
        }
    }
    .category-list h5 {
        color: #888;
        font-size: 1.2rem;
        margin-bottom:.4rem;
        font-weight: normal;
    }
}

</style>





