<template>
    <div class="category">
        <header>
            <router-link to="search">
                <div class="input">
                    <i class="iconfont icon-search"></i>
                    /
                </div>
            </router-link>
        </header>
        <aside>
            <swiper 
                class="category__swiper"
                :options="swiperOption"
                ref="categorySwiper"
                v-show="bCategoryList">
                <swiper-slide 
                    v-for="(item, index) in categoryList" 
                    :key="'categoryList' + index" 
                    :class="{
                        active: index === nCategoryActive && nActive == 0,
                        active1:index === nCategoryActive && nActive == 1
                    }" 
                    @click.native="toggleCategory(index, item.pid, $event)">
                    <a href="javascript:;"> {{ item.cat_alias_name || item.cat_name }} </a>
                    <ul 
                        v-if="item.child_tree && item.child_tree.length > 0" 
                        :style="{
                            height:index === nCategoryActive 
                                && nActive == 1 
                                ? childrenUlHen 
                                : '0px'
                        }">
                        <li 
                            v-for="(childrenItem, childrenIndex) in item.child_tree" 
                            :key="'categoryListIndex' + index + childrenIndex" 
                            :class="{
                                active: index === nCategoryActive 
                                    && childrenIndex === nCategoryChildrenActive
                            }" 
                            @click.stop="toggleCategoryChildren(childrenIndex,childrenItem.cat_id)">
                            {{ childrenItem.cat_alias_name || childrenItem.cat_name }}
                        </li>
                    </ul>
                </swiper-slide>
            </swiper>
        </aside>
        <section 
            class="category__con" 
            :class="{
                'category__con-no': !bCategoryList
            }">
            <h5 v-show="bCategoryList">{{ h5Title }}</h5>    
            <ul class="product-list small">
                <li
                    v-for="(item, index) in productList"
                    :key="item.id || item.goods_id">
                    <router-link 
                        :to="{
                            path:`goods/${item.id || item.goods_id}`
                        }"></router-link>
                    <computed-num 
                        @computed-num="computedNum"
                        :options="{
                            index,
                            id:item.id || item.goods_id,
                            name:item.name || item.goods_name,
                            price:item.shop_price,
                            number:item.number,
                            moreAttr:item.attr_type == 1 ? true : false
                        }">
                    </computed-num>
                    <figure>
                        <div class="img-box">
                            <img v-lazy="item.goods_img" />
                        </div>
                        <figcaption>
                            <h4>{{ item.name || item.goods_name }}</h4>
                            <p class="price">
                                <em> {{ item.shop_price }} </em>
                            </p>
                        </figcaption>
                    </figure>
                </li>
            </ul>
            <div class="pro-load" v-show="bProLoad"></div>
            <div class="no-prolist" v-show="bNoProlist">
                ~
            </div>
        </section>
        <footer-cart 
            :bCartlist.sync="bCartList" 
            :b-cart-list="bCartList"
            :options="footerInfo">
        </footer-cart>
        <transition name="cartlist">
            <keep-alive>
                <cart-list 
                    v-if="bCartList"
                    @computed-cart="computedCart"
                    @get-cart-list-arr="getCartListArr"
                    @update-goods-number="updateGoodsNumber"
                    @close-cart-list="closeCartList"
                    @clear-goods-number="clearGoodsNumber">
                </cart-list>
            </keep-alive>
        </transition>
        <transition name="attr">
            <category-attr 
                :data="attrOption"
                :attr.sync="bProductAttr"
                @updateNumber="updateNumber"
                v-if="bProductAttr">
            </category-attr>
        </transition>
        <transition name="mask-fade">
            <div 
                class="mask" 
                @click="closeMask()" 
                :style="{
                    zIndex:bProductAttr ? 124 : 122
                }"
                v-if="bProductAttr || bCartList">
            </div>  
        </transition>
        <ec-confirm :option="confirmOption" :visible.sync="showConfirm"></ec-confirm>
    </div>
</template>

<script>
// node library
import qs from 'qs'

// custom components
import CategoryAttr from 'components/CategoryAttr'
import FooterCart from 'components/FooterCart'
import CartList from 'components/CartList'
import ComputedNum from 'components/ComputedNum'
import EcConfirm from 'components/Confirm'

//goods
import {
    getCartInfo
} from '../config/getPostGoods'

// third party components
import {
    swiper,
    swiperSlide
} from 'vue-awesome-swiper'

import {
    Loading
} from 'element-ui'

import bus from '../eventBus'

export default {
    name: 'category',
    components: {
        swiper,
        swiperSlide,
        CategoryAttr,
        FooterCart,
        CartList,
        ComputedNum,
        Loading,
        EcConfirm
    },
    data() {
        return {
            swiperOption: {
                notNextTick: true,
                direction: 'vertical',
                slidesPerView: 'auto',
                freeMode: true
            },
            attrOption: {
                attr:[],
                name:"",
                id:"",
                price:""
            },
            resArr:[],// id 
            nCategoryActive: -1,
            nCategoryChildrenActive: 0,
            nActive: 0,
            childrenUlHen: 0,
            h5Title:'',
            categoryList: [],
            bCategoryList:true,
            loadingInstance:null,
            productList:[],
            bProLoad:false,
            bNoProlist:false,
            cateid:0,
            currentPage:1,
            bOnload: false,
            bProductAttr:false,
            bCartList:false,
            keywords:'',
            footerInfo:{
                number:'0',
                price:'0'
            },
            cartList:[],
            confirmOption:{
                title:"",
                context:""
            },
            showConfirm:false
        }
    },
    created() {
        this.loadingInstance = Loading.service({ target: "body", text: "" });
        this.$http.post(
            this.url('category&c=index&a=simple')
        ).then(({ data: { category } }) => {
            this.categoryList = category
            this.$nextTick(() => {
                if (this.categoryList.length > 0) this.toggleCategory(0)
            })
        })
    },
    mounted() {
        document.addEventListener('scroll',this.asynScrollPro,false)
    },
    beforeRouteEnter(to, from, next){
        if (from.name != "goods") {
            if (to.query.keyword) {
                next(vm => {
                    vm.bCategoryList = false
                    vm.currentPage = 1
                    vm.keywords = to.query.keyword
                    vm.getCartInfo().then(o => {
                        vm.footerInfo.number = o.number
                        vm.footerInfo.price = o.price
                    })
                    vm.productList.map(item => {
                        vm.cartList.map(item1 => {
                            if (item.goods_id == item1.goods_id) {
                                item.goods_number = item1.goods_number
                            }
                        })
                    })
                })
            } else {
                next(vm => {
                    vm.getCartInfo().then(o => {
                        vm.footerInfo.number = o.number
                        vm.footerInfo.price = o.price
                    })
                    vm.bCategoryList = true
                })
            }
        }else{
            next();
        }
    },
    methods: {
        /**
         * number
         *
         */
        clearGoodsNumber(){
            this.productList.forEach(item => {
                item.number = 0
            })
            this.cartList = []
        },
        /**
         * cartlist
         */
        closeCartList(val){
            this.bCartList = val
        },
        /**
         *  carlist  goodsId  cagegory
         * goodsList  nunber 
         * 
         * @param {Object} o - goodsId
         */
        updateGoodsNumber(o){
            let len = this.productList.length
            for (let i = 0; i < len; i++){
                if(this.productList[i].goods_id == o.goodsId){
                    o.type == 'plus' ? this.productList[i].number++ : this.productList[i].number--
                    break;
                }
            }
        },
        /**
         *  id 
         */
        getCartListArr(arr){
            this.resArr = arr
            if(this.resArr && this.resArr.length == 0){
                this.bCartList = false 
            }
        },
        /**
         * 
         */
        getCartInfo(){
            return new Promise((resolve, reject) => {
                this.$http.post(this.url('cart')).then(({ data }) => {
                    let arr = []
                    data.goods_list && data.goods_list.forEach(item => {
                        item.goods_list && item.goods_list.forEach(item => {
                            arr.push({
                                goods_id: item.goods_id,
                                goods_number: item.goods_number,
                                rec_id: item.rec_id
                            })
                        })
                    })
                    this.cartList = arr
                    
                    resolve({
                        number: data.cart_show.cart_goods_number || 0,
                        price: data.total.goods_price || '0.00'
                    })
                })
            })
        },
        /**
         * 
         * 
         * @param {Object} o -  CartList.vue 
         */
        computedCart(o){// and 
            this.footerInfo.number = o.number
            this.footerInfo.price = o.price
        },
        /**
         * 
         */
        closeMask(){
            if(this.bProductAttr) this.bProductAttr = false
            if(this.bCartList) this.bCartList = false
        },
        preventDefault(e){
            e.preventDefault()
        },
        /**
         *  category 
         * cartList
         * 
         * @param {Object} o - 
         */
        updateNumber(o){
            this.cartList.unshift({
                goods_id:o.id,
                goods_number:o.addNumber
            })
            this.footerInfo.number = o.goodsNumber
            this.footerInfo.price = o.shopPrice.slice(0,1) + (parseFloat(this.footerInfo.price.slice(1))+parseFloat(o.shopPrice.slice(1))).toFixed(2)
            this.productList[o.index].number = o.number
        },
        asynScrollPro(){//
            let height = (document.body.clientHeight - window.innerHeight) - document.body.scrollTop
            if( height <= 200 && height >= 0){
                this.bOnload = true
            }
        },
        /**
         *  category.vue 
         * 
         * 
         * @param {String} type -  plus  less
         * @param {Object} o - getProductAttr
         */
        computedNum(type,o){
            if (type == 'less') {//
                let cartLen = this.cartList.length
                let recId = 0
                let arr = []
                let cartIndex = 0
                if (this.productList[o.index].number > 0) {
                    this.productList[o.index].number--
                }
                for(let i = 0; i<cartLen; i++){
                    if(this.cartList[i].goods_id == o.id){
                        cartIndex = i
                        recId = this.cartList[i].rec_id
                    }
                    arr.push(this.cartList[i].rec_id)
                }
                if(this.productList[o.index].number > 0){
                    this.$http.post(this.url('cart&a=cart_goods_number&ajax=1'),qs.stringify({
                        id:recId,
                        number:this.productList[o.index].number,
                        arr:arr.join(','),
                        none:0
                    })).then(({data:{error}}) => {
                        if(error == 0){
                            let market = ''
                            this.cartList[cartIndex].goods_number--
                            this.footerInfo.number--
                            market = o.price.slice(0,1)
                            this.footerInfo.price = market + (parseFloat(this.footerInfo.price.slice(1))-parseFloat(o.price.slice(1))).toFixed(2)
                        }
                    })
                }else{
                    this.$http.post(this.url('cart&a=delete_cart&ajax=1'),qs.stringify({
                        id:recId
                    })).then(({ data:{ error } }) => {
                        if(error == 0){
                            this.$delete(this.cartList,cartIndex)
                            this.footerInfo.number--
                            this.footerInfo.price = o.price.slice(0,1) + (parseFloat(this.footerInfo.price.slice(1))-parseFloat(o.price.slice(1))).toFixed(2)
                        }
                    })
                }
            } else {//
                if (this.productList[o.index].number < 99) {
                    this.getProductAttr(o,o.index).then(resolve => {
                        if(resolve){
                            this.bProductAttr = true
                        }else{
                            this.$http.post(this.url('cart&a=add_to_cart'),qs.stringify({
                                goods:JSON.stringify({
                                    spce:[],
                                    goods_id:o.id,
                                    number:1
                                })
                            })).then(({ data }) => {
                                if(data.error == 0){
                                    this.footerInfo.number = data.goods_number
                                    this.$set(this.productList[o.index], 'cart_id', data.cart_value)
                                    if (o.number == 0) {
                                        this.getCartInfo()
                                    }
                                    this.productList[o.index].number++
                                    this.footerInfo.price = o.price.slice(0,1) + (parseFloat(this.footerInfo.price.slice(1))+parseFloat(o.price.slice(1))).toFixed(2)
                                }
                                if(data.error == 2){
                                    this.confirmOption.context = ''
                                    this.showConfirm = true
                                }
                            })
                            
                        }
                    })
                } else {
                    this.confirmOption.context = ' 99 '
                    this.showConfirm = true
                }
            }
        },
        /**
         * 
         */
        toggleCategory(index, loadingInstance, e) {
            let children = this.categoryList[index].child_tree,
                bChildren = children && children.length > 0,
                el = e && e.target,
                nNextSiblingH = 0,//
                nextSibling = (el && el.nextElementSibling)
                    || this.$el.querySelector(`.category__swiper .swiper-slide:nth-of-type(${index + 1}) ul`)


            if (bChildren) {
                this.nCategoryChildrenActive = 0
                let id = children[0].cat_id
                if(this.cateid != id){
                    this.productList = []
                    this.currentPage = 1
                    this.bOnload = false
                }
                this.cateid = id
                if (this.nActive == 0 || this.nCategoryActive != index) {
                    this.nActive = 1
                    if (nextSibling) {
                        nextSibling.querySelectorAll('li').forEach(item => {
                            nNextSiblingH = nNextSiblingH + parseFloat(getComputedStyle(item).height)
                        })
                    }
                } else {
                    this.nActive = 0
                }
                this.childrenUlHen = nNextSiblingH + 'px'
                this.h5Title = children[this.nCategoryChildrenActive].cat_name
                if(this.nCategoryActive != index){
                    this.getProduct(children[this.nCategoryChildrenActive].cat_id).then(resolve => {
                        this.loadingInstance.close()
                    })
                }
                this.nCategoryActive = index
            } else {
                this.h5Title = this.categoryList[index].cat_name
                this.nActive = 0
                this.nCategoryActive = index
                this.bNoProlist = true
            }
            setTimeout(() => {
                this.swiper.slideTo(index)
            }, 10);
        },
        /**
         *  goodslist
         *  
         */
        toggleCategoryChildren(index, id) {
            this.bNoProlist = false
            if(this.cateid != id){
                this.productList = []
                this.currentPage = 1
                this.bOnload = false
                document.addEventListener('scroll',this.asynScrollPro,false)                
            }
            this.cateid = id
            if (this.nCategoryChildrenActive != index) {
                this.nCategoryChildrenActive = index
                this.h5Title = this.categoryList[this.nCategoryActive].child_tree[index].cat_name
                this.getCartInfo().then(() => {
                    this.getProduct(id).then(() => {
                        this.bProLoad = false
                    })
                })
            }
        },
        /**
         * 
         * 
         * @param {Number} id - Id
         */
        getProduct(id){
            this.bProLoad = true
            let loadingInstance = Loading.service({ target: ".pro-load", text: "" })
            if(this.bCategoryList){
                return this.$http.post(this.url('category&c=index&a=goods'),qs.stringify({
                    cat_id: id,
                    currentPage: this.currentPage
                })).then(({data:{goodslist}}) => {
                    loadingInstance.close()
                    this.cumputendGoodsList(goodslist)
                })
            }else{
                return this.$http.post(this.url('search&c=index&a=segoods'),qs.stringify({
                    kwords: this.keywords,
                    currentPage: this.currentPage
                })).then(({data:{goods_list}}) => {
                    loadingInstance.close()
                    this.cumputendGoodsList(goods_list)
                })
            }
        },
        /**
         * goods list
         */
        cumputendGoodsList(goodslist){
                goodslist && goodslist.forEach(item => {
                    let number = 0
                    this.cartList && this.cartList.forEach(itemCart => {
                        let nGoodsId = 1
                        if(item.goods_id == itemCart.goods_id){
                            number += parseInt(itemCart.goods_number)
                        }
                    })
                    item.number = number
                })

                this.productList.push(...goodslist)
                if(goodslist.length < 10){
                    this.bNoProlist = true
                    document.removeEventListener('scroll',this.asynScrollPro,false)
                }else{
                    this.bNoProlist = false
                }
                this.currentPage++
        },
        /**
         * 
         * 
         * @param {Object} - 
         * @param {Number} - index - 
         */
        getProductAttr(o,index){
            let loadingInstance = Loading.service({ target: ".product-list", text: "" })
            return new Promise((resolve, reject) => {
                this.$http.post(this.url('category&c=index&a=attr'), qs.stringify({
                    goods_id: o.id,
                })).then(({ data: { attr } }) => {
                    if (!(attr instanceof Array)){
                        this.attrOption.id = o.id
                        this.attrOption.name = o.name
                        this.attrOption.price = o.price
                        this.attrOption.index = index
                        this.attrOption.number = o.number
                        this.attrOption.attr = attr
                        setTimeout(() => {
                            loadingInstance.close()
                            resolve(true)
                        }, 100)
                    }else{
                         setTimeout(() => {
                            loadingInstance.close()
                            resolve(false)
                        }, 100)
                    }
                })
            })
        }
    },
    computed: {
        swiper() {
            return this.$refs.categorySwiper.swiper
        }
    },
    activated () {
        // setTimeout(() => {
        //     this.productList.map(item => {
        //         let number = 0
        //         this.cartList.map(item1 =>{
        //             if(item.goods_id == item1.goods_id){
        //                 number += parseInt(item1.goods_number)
        //                 item.number = parseInt(number)
        //             }
        //         })
        //     })
        // }, 300);
    },
    watch: {
        'bOnload'(val, oldVal) {
            if (val) {
                this.bNoProlist = false
                this.getProduct(this.cateid).then(resolve => {
                    this.bOnload = false
                })
            }
        },
        'bProductAttr'(val,oldVal){
            if(val){
                document.body.addEventListener('touchmove',this.preventDefault,false)
            }else{
                document.body.removeEventListener('touchmove',this.preventDefault,false)
            }
        },
         $route() {
            document.removeEventListener('scroll',this.asynScrollPro,false)
            if (this.$route.name && (this.$route.name == 'category' || this.$route.name == 'home')) {
                 this.productList = []
                 this.currentPage = 1
                 document.addEventListener('scroll',this.asynScrollPro,false)
                 if (this.$route.query.hasOwnProperty('keyword')) {
                     this.bCategoryList = false
                     this.keywords = this.$route.query.keyword
                     this.getCartInfo().then((o) => {
                        this.footerInfo.number = o.number
                        this.footerInfo.price = o.price
                        this.getProduct()
                     })
                 } 
                 else {
                    this.bCategoryList = true
                    this.getCartInfo().then((o) => {
                        this.footerInfo.number = o.number
                        this.footerInfo.price = o.price
                        this.getProduct(this.cateid)
                    })
                 }
             }
        }
    }
}
</script>
<style lang="scss" scope>
@import '../assets/style/config';
@import '../assets/style/mixins/common';
body {
    background: #fff;
}

.el-loading-mask{
    background-color: rgba(255, 255, 255, 1) !important;
}

.loading-mask-goods{
    background-color: rgba(255, 255, 255, 1) !important;
}

.attr-loadding{
    background-color: rgba(255, 255, 255, .4) !important;
}

.category {
    padding-top: 3.6rem;
    padding-bottom:5.4rem;
    > header {
        padding: .6rem 1rem;
        background: #fff;
        border-bottom: 1px solid #e7ecec;
        position: fixed;
        top: 0;
        width: 100%;
        z-index: 6;
    }
    .input {
        width: 100%;
        height: 3.2rem;
        line-height: 3.2rem;
        background: #eee;
        color: #777;
        font-size: 1.3rem;
        border-radius: 4px;
        position: relative;
        padding-left: 3.2rem;
        padding-right: 3.2rem;
        i {
            position: absolute;
            left: 1.1rem;
        }
        a {
            @include urlAbsolute();
        }
    }
    aside {
        position: fixed;
        top: 4.4rem;
        bottom: 4.4rem;
    }
}

.category__con {
    background: #fff;
    margin-left: 7.2rem;
    margin-top: 4.4rem;
    box-sizing: border-box;
    padding: .6rem;
    padding-bottom:2rem;
    position:relative;
    min-height:26rem;
    h5 {
        position:fixed;
        z-index: 11;
        font-weight: normal;
        font-size: 1.3rem;
        height: 3rem;
        line-height: 3rem;
        background:#f3f3f3;
        padding:0 1.2rem;
        left:7.2rem;
        top:4.4rem;
        right:0;
        color:#666;
    }
    ul {
        width: 100%;
        overflow: hidden;
         .el-loading-mask{
            position:absolute;
            top:16rem;
            left:0;
            bottom:initial;
            background:rgba(0, 0, 0, 0.2) !important;
            .el-loading-spinner .circular{
                width:30px;
                height:30px;
            }
        }
        li {
            padding: 1.2rem .5rem;
            border-bottom:1px solid #f3f3f3;
            position:relative;
            z-index: 0;
            &:last-of-type{
                border-bottom:none;
            }
            &:first-of-type{
                padding-top:0;
            }
            img {
                max-width: 100%;
                max-height: 100%;
            }
            >span {
                font-size: 1.3rem;
                color: #777;
                display: block;
                width: 100%;
                height: 2.4rem;
                line-height: 2.4rem;
                text-align: center;
            }
            .to-cart{
                position: absolute;
                right: 1rem;
                bottom: 1rem;
                z-index: 11;
            }
            a{
                @include urlAbsolute()
            }
        }
    }
}
.category__con-no{
    margin-left: 0rem;
    margin-top: 1rem;
    overflow-x: hidden;
}

.category__swiper {
    min-height: 100%;
    max-height: 100%;
    background: #f3f3f3;
    font-size: 1.3rem;
    width: 7.2rem;
    text-align: center;
    & .swiper-slide {
        z-index: 11;
        & a {
            display: block;
            padding: 1.3rem .6rem;
            box-sizing: border-box;
            color: #333;
            border-right: 1px solid #e5e5e5;
            border-bottom: 1px solid #e5e5e5;
            white-space: nowrap;
            @include ellipses();
        }
        &:last-child {
            a {
                border-bottom: none;
            }
        }
        & ul {
            height: 0;
            overflow: hidden;
            visibility: hidden; 
            // @include transition(all,.4s);
        }
        &.active1 {
            border-right: 0;
            & a {
                color: $color;
            }
            & ul {
                overflow: auto;
                visibility: visible;
                //@include transition(all,.4s);
            }
        }
        & ul li {
            font-size: 1.3rem;
            padding: 1.3rem .8rem;
            background: #fff;
            position: relative;
            border-right: 1px solid #e5e5e5;
            border-bottom: 1px dashed #e5e5e5;
            &.active {
                color: $color;
                border-left: 2px solid $color;
                border-right: 0;
                position: relative;
            }
        }
        &.active {
            border-right: none;
            & a {
                background: #fff;
                border-right: 0;
                border-left: 2px solid $color;
                color: $color;
            }
        }
    }
}


.product-list {
    overflow: hidden;
    &.small{
        .preview{
            h4{
                height: inherit
            }
        }
    }
    ul{
        li{
            width: 100%;
            padding-top: 1px;
            padding-right: 1px;
            float: left;
            position: relative;
            a{
                position: absolute;
                left: 0;
                top: 0;
                right: 0;
                bottom: 0;
                z-index: 2;
            }
        }
    }
    figure{
        background: #fff;
        box-sizing: border-box;
        width: 100%;
        @include box-pack(center, start);
        @include direction(center, flex-start);
        img{
            width: 100%;
            height: auto;
        }
        h4{
            color:#333;
            font-size: 1.4rem;
            height: 4rem;
            font-weight: normal;
            overflow:hidden;
            margin-bottom:.9rem;
        }
        .price em{
            color: $color;
            font-size: 1.6rem;
            display: block;
        }
        .remark em{
            font-size: 1.3rem;
            color: #888;
        }
    }
    figcaption{
        margin-top: .8rem;
        width: 60%;
    }
}


.product-list.small li figure .img-box{
    margin-right:1rem;
    width:6.8rem;
    height:6.8rem;
    text-align: center;
}

.product-list.small li img {
    max-width:100%;
    max-height:100%;
    padding: .2rem;
    margin-right: .8rem;
    height: auto;
}

.product-list.small li figcaption {
    margin-top: 0;
    @include flex1-1();
}
.mask{
    position:fixed;
}
.pro-load {
    position:absolute;
    width:100%;
    top:1.6rem;
}
.pro-load svg{
    display:none
}
.no-prolist{
    margin-top:1rem;
    width:100%;
    text-align: center;
    font-size:1.3rem;
    color:#999;
}

.mask-fade-enter-active, .mask-fade-leave-active,.cartlist-enter-active,.cartlist-leave-active{
    transition: all .3s
}
.mask-fade-enter, .mask-fade-leave-to /* .fade-leave-active in below version 2.1.8 */ {
  opacity: 0
}

.attr-enter-active,.attr-leave-active{
    transition:all .4s;
}
.attr-enter,.attr-leave-to{
    transform: scale(.4);
    opacity: 0;
}

.attr-leave,.attr-enter-to{
    transform: scale(1);
}

.cartlist-enter, .cartlist-leave-to /* .fade-leave-active in below version 2.1.8 */ {
  transform: translateY(100%)
}

.cartlist-leave,.cartlist-enter-to{
   transform: translateY(0)
}

</style>




